*{
  font-size: 12px;
}

.App{
  
}

#body{
  font-family: 'Titillium Web', sans-serif;
  
  width: 900px;
  background-color: whitesmoke;
  margin-left: auto;
  margin-right: auto;
  justify-content: center;
}

.nav-container{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 20px;
}

#pvwatts-header-text{
  font-size: 3.5em;
  font-weight: 200;
}

.blue-bar{
 width: 680px;
 height: 3px; 
 background-color: blue;

 margin-left: auto;
 margin-right: auto;


}



#holding-square{
  
  background-color: white;
  width: 700px;
  
  margin-left: auto;
  margin-right: auto;
  justify-content: center;  

}

main{
  margin-top: 40px; 
}


h1{
  color: rgb(255, 94, 0); 
  font-weight: 600;
  font-size: 3em;
  text-transform: uppercase;
  text-align: start;
  padding: 20px;
}

.slide{
  width: 700px;
  height: 650px;
  margin-left: auto;
  margin-right: auto;
  justify-content: center; 
  text-align: center;

  background-color:white;

}

#annual-output-num{
  font-size: 4em;
  font-weight: 400;
  color: blue;
}

#results-header{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 0 30px;
}

#data-values{
  
  background-color: rgb(224, 224, 224);
  font-size: 40px;
  width: 250px;
  border-radius: 20px;
  border: 1px solid black;
  margin-left: auto;
  margin-right: auto;
  justify-content: center; 
  border-bottom: 25px;

}

#dropdown-holder{
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding: 0 30px;
}

.dropdown{
  margin: 20px;
} 

#home-img{
  display: block;
  margin-left: auto;
  margin-right: auto;
  border-radius: 15px;
  width: 300px;
}

#kwh-year{
font-size: 3em;
color: rgb(87, 87, 87);
}

Table{
  border: darkgray 1px solid; 
  background-color: rgb(176, 222, 238);
}

.carousel-control-next-icon {
  background-color: orange;
  font-size: 3em;
  
}

.carousel-control-prev-icon {
  background-color: orange;
}

.footer{
  width: 700px;
  justify-content: center;
  text-align: center;
}

#footer-links{
  font-weight: 600;
}

#nrel-description{
  margin-top: 2em;
  font-size: 1em;
  background-color: rgb(211, 209, 209);
  padding: 0 15px;
}